<template>
  <div class="text-center">
    <ul class="list-group">
      <li class="list-group-item list-group-item-warning">Recadastramentos realizados</li>
      <template v-if="recadastramentos.length">
        <li v-for="r in recadastramentos" :key="r.data" class="list-group-item">
          {{corrigeData(r.data)}}
        </li>
      </template>
      <li v-else class="list-group-item">Nenhum registro</li>
    </ul>
    <button class="btn btn-primary my-3" @click="$router.go(-1)"><i class="fas fa-long-arrow-alt-left pr-2"/>VOLTAR</button>
  </div>
</template>

<script>
  import DateHelper from "../../helpers/DateHelper";
  export default {
    name: "Historico",
    data: () => ({
      recadastramentos: []
    }),
    methods: {
      corrigeData(datetime) {
        return DateHelper.datetime_SQLparaBR(datetime);
      }
    },
    created() {
      if (this.$store.state.recadastramentos) this.recadastramentos = this.$store.state.recadastramentos;
    }
  }
</script>

